<script>

let status = 'component loaded'

AFRAME.registerComponent('model-click', {
    init: function(){
        const el = this.el

        el.addEventListener('model-loaded', function(){
            status = 'model loaded'
        })

        if(status === 'model loaded'){
            el.addEventListener('click', this.handleClick)
        }
    },
    handleClick: function(ev){
        ev.stopPropagation();
        ev.preventDefault();
		//const method = ev.target.getAttribute('id')
        const el = ev.detail.intersection && ev.detail.intersection.object.el;

        if (el && el === ev.target) {
            // do something
            console.log('click')
        }
    }
})

const modelExt = "obj"
const objModelUrl = "obj: url(./static/models/chair/jean_obj.obj); mtl: url(./static/models/chair/jean_obj.mtl)"
const gltfModelUrl="url(/path/to/tree.gltf)";

</script>

{#if modelExt === "obj"}
    <a-text value="Model Loading" visible={status === "component loaded"} rotation="-90 0 0" width='1' height="1" positon="-0.5 0 0"></a-text>
    <a-entity visible={status === "model loaded"} model-click obj-model={objModelUrl} scale="1 1 1" rotation="-90 0 0"></a-entity>
{:else if modelExt === "gltf" || modelExt === "glb"}
    <a-text value="Model Loading" visible={status === "component loaded"} rotation="-90 0 0" width='1' height="1" positon="-0.5 0 0"></a-text>
    <a-entity visible={status === "model loaded"} model-click gltf-model={gltfModelUrl} scale="1 1 1" rotation="-90 0 0"></a-entity>
{/if}